.page404 {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  text-align: center;
}

.page404 .message-box h1,
.page404 .message-box p {
  font-size: 5rem;
}

.effect2 {
  display: inline-block;
  position: relative;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  border-radius: 6px;
  background-color: #226500;
  text-transform: capitalize;
  padding: 0.5rem;
  width: 160px;
  color: #fff;
  text-align: center;
}

.effect2.effect-2 {
  transition: all 0.25s linear;
}

.effect2.effect-2::before {
  content: "ðŸ ”";
  font-size: 25px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 6px;
  top: 0;
  opacity: 0;
  height: 100%;
  width: 48px;
  transition: all 0.25s linear;
}

.effect2.effect-2:hover {
  text-indent: 25px;
  color: #fff;
}

.effect2.effect-2:hover::before {
  opacity: 1;
  text-indent: -20px;
}

.buttons-con a {
  width: 250px;
}

.buttons-con button {
  border: none;
}

@media screen and (max-width: 767px) {
  .page404 .message-box h1,
  .page404 .message-box p {
    font-size: 4.5rem;
  }
}
